<template>
	<!--部门管理界面-->
	<!--整体外框：-->
	<div class="d-box v-box">
		<!--1、head头部-->
		<div class="headDiv">
			<div class="headerLeft">
				<span class="spanclass">组织名称</span>
				<el-input placeholder="请输入组织名称" v-model="head.inputOrganName" clearable>
				</el-input>
			</div>
			<div class="headerLeft">
				<span class="spanclass">组织编码</span>
				<el-input placeholder="请输入组织编码" v-model="head.inputOrganCode" clearable></el-input>
			</div>
			<div class="clearfix">
				<div class="headRight">
					<SolidButton :type='"solid"' :text='"查询"' @click="queryClick"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="resetClick"></SolidButton>
				</div>
			</div>
			<!--2、列表头部-->
			<div class="iconButton clearfix">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="addClick"></Button>
				<Button :type='out.type' :icon='out.icon' :text='out.text' @click="deleteClick"></Button>
				<Button :type='edit.type' :icon='edit.icon' :text='edit.text' @click="editClick"></Button>
			</div>
		</div>

		<div class="flexTable">
			<Table :data='rows' :tableName='tableName' @selection-change="handleSelectionChange" :currentPage1="currentPage" :num='pageTotal' @size-change="handleSizeChange" @current-change="handleCurrentChange"></Table>
		</div>

		<!--5、弹窗-->
		<el-dialog :title="pop.title" :visible.sync="pop.centerDialogVisible" width="600px" center>
			<el-form class="clearfix" :model="pop">
				<el-form-item :label-position="pop.labelPosition" label="组织名称" :label-width="pop.formLabelWidth">
					<el-input v-model="pop.name" placeholder="请输入组织名称" auto-complete="off" clearable></el-input>
				</el-form-item>
				<el-form-item class="lastformItem" :label-position="pop.labelPosition" v-bind:style="{display:pop.isHidden}" label="组织编码" :label-width="pop.formLabelWidth">
					<el-input v-bind:style="{display:pop.isHidden}" placeholder="请输入组织编码" v-model="pop.code" auto-complete="off" clearable></el-input>
				</el-form-item>

				<el-form-item :class="pop.isDisable?'lastformItem':''" :label-position="pop.labelPosition" label="上级组织" :label-width="pop.formLabelWidth">
					<!--可输入搜索的select框-->
					<el-select class="selectwidth" :disabled="pop.isDisable" v-model="pop.pnamecode" filterable placeholder="请选择上级组织">
						<!--这里的value值对应的上面的v-model的默认值-->
						<el-option v-for="item in pop.supOrgans" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :class="pop.isDisable?'':'lastformItem'" :label-position="pop.labelPosition" label="组织类别" :label-width="pop.formLabelWidth">
					<el-select class="selectwidth" v-model="pop.accountStatus" filterable placeholder="请选择组织类别">
						<el-option v-for="item in pop.accountSystems" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"hollow"' :text='"取消"' @click="pop.centerDialogVisible = false"></SolidButton>
				<SolidButton :type='"solid"' :text='"确定"' @click="popSureClick"></SolidButton>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
	import Table from '../../table/Table.vue';

	export default {
		components: {
			Button,
			SolidButton,
			Table,
		},
		data() {
			return {
				//组件化
				add: {
					text: '新增',
					type: 'icon-add',
					icon: 'icon-add',
				},
				out: {
					text: '删除',
					type: 'icon-delete',
					icon: 'icon-delete',
				},
				edit: {
					text: '编辑',
					type: 'icon-edit',
					icon: 'icon-edit',
				},
				//1、head头部
				head: {
					inputOrganName: "",
					inputOrganCode: ""
				},
				//2、列表头部
				//3、main内容
				rows: [], //所有的data数组
				multipleSelection: [], //列表选择的数组
				ids: [], //for循环后id的合集
				//表格对应的字段
				tableName: [{
						value: 'name',
						name: "组织名称"
					},
					{
						value: 'code',
						name: "组织编码"
					},
					{
						value: 'pname',
						name: '上级组织'
					},
					{
						value: 'crtTime',
						name: '创建时间'
					}
				],
				//4、底部分页
				currentPage: 1, //当前页数
				pageRows: 10, //每页
				pageTotal: 0, //总数

				//5、弹窗
				pop: {
					//基本配置
					centerDialogVisible: false, //弹窗显示和隐藏
					labelPosition: 'left',
					title: "",
					formLabelWidth: '70px',
					//区分配置
					isHidden: "block",
					isDisable: false,
					popType: '',
					//界面参数
					name: '',
					code: '',
					pnamecode: '', //传接口的pnamecode
					accountStatus: '',
					//选择器
					supOrgans: [], //上级组织列表（接口请求到的）
					accountSystems: [{
						name: "总公司",
						id: 1
					}, {
						name: "分公司",
						id: 2
					}] //账户状态select
				},
			}
		},
		methods: {
			loadData() {
				var self = this;
				//1、列表接口
				var p = {
					pageNum: self.currentPage,
					pageRows: self.pageRows,
					name: self.head.inputOrganName,
					code: self.head.inputOrganCode
				}
				self.$api.post('org/doOrgList', p, r => {
					console.log(r);
					self.rows = r.repData.list
					self.pageTotal = r.repData.total
				}, e => {
					console.log(e);
				})
			},
			requestDeptList() {
				var self = this;
				//2、上级组织list接口
				var p = {}
				self.$api.post('org/doOrgList', p, r => {
					console.log(r);
					self.pop.supOrgans = r.repData.list;
				}, e => {
					console.log(e);
				})
			},
			//1、head头部
			queryClick() {
				var self = this;
				self.loadData();
			},
			resetClick() {
				var self = this;
				self.head.inputOrganName = "";
				self.head.inputOrganCode = "";
				self.loadData()
			},
			//2、列表头部
			deleteClick() {
				if(this.multipleSelection.length == 0) {
					this.$message({
						message: '未勾选数据',
						type: 'warning'
					});
				} else {
					this.$confirm('确定删除勾选数据吗？', '', {
						confirmButtonText: '是',
						cancelButtonText: '否',
						type: '',
						center: true
					}).then(() => {
						this.deleteMethod();
					}).catch(() => {});
				}
			},
			deleteMethod() {
				var self = this;
				//参数赋空
				self.ids = [];
				for(var i = 0; i < self.multipleSelection.length; i++) {
					self.ids.push(self.multipleSelection[i].id)
				}
				console.log(self.ids)
				//3、删除接口
				var p = {
					ids: self.ids
				}
				self.$api.post('org/doOrgDelete', p, r => {
					console.log(r);
					this.$message({
						message: '删除成功',
						type: 'success'
					});
					self.loadData();
					self.requestDeptList();
				}, e => {
					console.log(e);
				})
			},
			//新增按钮
			addClick() {
				var self = this;
				//窗口配置
				self.pop.centerDialogVisible = true;
				//区别
				self.pop.title = "新增组织";
				self.pop.popType = "add"
				self.pop.isDisable = false;
				self.pop.isHidden = "block"
				//字段赋空
				self.pop.name = "";
				self.pop.code = "";
				self.pop.pnamecode = "";
				self.pop.accountStatus = "";
			},
			//编辑按钮
			editClick() {
				var self = this;
				if(self.multipleSelection.length == 1) {
					//编辑窗口配置
					self.pop.centerDialogVisible = true
					//区别
					self.pop.title = "编辑组织"
					self.pop.popType = "edit"
					self.pop.isHidden = "none"
					self.pop.isDisable = true
					//参数赋值
					self.pop.name = self.multipleSelection[0].name;
					self.pop.pnamecode = self.multipleSelection[0].pId;
					self.pop.accountStatus = self.multipleSelection[0].orgType

				} else {
					this.$message({
						message: '请勾选单条数据',
						type: 'warning'
					});
				}
			},
			//5、弹窗
			//确定按钮
			popSureClick() {
				var self = this;
				if(self.pop.popType == "add") {
					//4、新增接口
					//空值判断
					if(self.pop.name.length == 0) {
						this.$message({
							message: '请输入组织名称',
							type: 'warning'
						});
						return
					}
					if(self.pop.code.length == 0) {
						this.$message({
							message: '请输入组织编码',
							type: 'warning'
						});
						return
					}
					if(self.pop.pnamecode.length == 0) {
						this.$message({
							message: '请选择上级组织',
							type: 'warning'
						});
						return
					}
					if(self.pop.accountStatus.length == 0) {
						this.$message({
							message: '请选择组织类别',
							type: 'warning'
						});
						return
					}
					var p = {
						name: self.pop.name,
						code: self.pop.code,
						pId: self.pop.pnamecode,
						orgType: self.pop.accountStatus
					}
					self.$api.post('org/doOrgInsert', p, r => {
						console.log(r);
						this.$message({
							message: '新增成功',
							type: 'success'
						});
						self.loadData();
						self.requestDeptList();
						self.pop.centerDialogVisible = false;
					}, e => {
						console.log(e);
					})
				} else {
					//5、编辑接口
					//空值判断
					if(self.pop.name.length == 0) {
						this.$message({
							message: '请输入组织名称',
							type: 'warning'
						});
						return
					}
					if(self.pop.accountStatus.length == 0) {
						this.$message({
							message: '请选择组织类别',
							type: 'warning'
						});
						return
					}
					var p = {
						id: self.multipleSelection[0].id,
						name: self.pop.name,
						code: self.multipleSelection[0].code,
						pId: self.pop.pnamecode,
						orgType: self.pop.accountStatus
					}
					self.$api.post('org/doOrgUpdate', p, r => {
						console.log(r);
						this.$message({
							message: '修改成功',
							type: 'success'
						});
						self.loadData();
						self.requestDeptList();
						self.pop.centerDialogVisible = false;
					}, e => {
						console.log(e);
					})
				}
			},
			//3、main内容
			//select行改变
			handleSelectionChange(val) {
				this.multipleSelection = val;
				console.log(val)
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//4、footer分页
			//每页多少条数据
			handleSizeChange(val) {
				var self = this;
				console.log("每页" + val);
				self.pageRows = val;
				self.loadData();
			},
			//当前第几页
			handleCurrentChange(val) {
				var self = this;
				console.log("当前页" + val);
				self.currentPage = val;
				self.loadData();
			}
		},
		//methods同级的初始化方法
		created: function() {
			this.loadData();
			this.requestDeptList();
		},
	}
</script>

<style scoped="scoped">
	/*0、整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		position: relative;
	}
	/*1、head头部*/
	/*head-left内容*/
	
	.headerLeft {
		width: auto;
		height: 40px;
		float: left;
		margin-right: 20px;
	}
	
	.spanclass {
		display: inherit;
		line-height: 40px;
		margin-right: 8px;
		float: left;
	}
	
	.headerLeft .el-input,.headerLeft .el-select {
		width: 188px;
		height: 40px;
	}
	/*head-right内容*/
	
	.headRight {
		float: right;
	}
	/*head按钮*/
	
	.iconButton {
		float: left;
		padding-top: 20px;
	}
	/*3、窗口*/
	
	.el-form-item {
		width: 49%;
		float: left;
	}
	
	.lastformItem {
		margin-left: 10px;
	}
	.selectwidth{
		width: 199.5px;
	}
</style>


